.container.about_container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}
.about_me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;
}

.about_me-image{
    border-radius: 2rem;
    overflow: hidden;
    transform:rotate(10deg);
    transition: var(--transition);
}

.about_me-image:hover{
    transform: rotate(0);
}

.about_content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

@media screen and(max-width:1024px) {
    .about_container{
        grid-template-columns: 1fr;
        gap: 0;

    }



    .about_me{
        width: 50%;
        margin: 2rem auto 4rem;    
    }

    .about_content p{
        margin: 1rem 0 1.5rem;
    }
    

}

    
@media screen and (max-width:600px) {
    .about_me {
        display: block; /* Ensure the image container is visible */
        width: 70%; /* Adjust the size to fit smaller screens */
        margin: 1rem auto; /* Center the image */
    }

    .about_me-image {
        bottom: 0; /* Reset positioning if needed */
        width: 100%; /* Ensure the image fills the container */
        height: auto; /* Maintain aspect ratio */
    }

    .container.about_container {
        grid-template-columns: 1fr; /* Simplify the layout */
        margin-bottom: 6rem;
    }

    .about_content {
        text-align: center;
        /* margin: 0 auto; */
        margin-top: -100px;
        margin-bottom: 100px;

        
    }

    .about_content p {
        margin: 1rem 0;
    }
}

